import React from 'react'
import { graphql, StaticQuery } from 'gatsby'
import "./styles.scss"

import Typical from 'react-typical'

import BackgroundImage from 'gatsby-background-image'

const BackgroundSection = () => {
  return (
    <StaticQuery
      query={graphql`
      query {
        indexImage: file(relativePath: { eq: "images/hero_img.jpg" }) {
          childImageSharp {
            fluid(quality: 90, maxWidth: 1920) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
      }
    `}
      render={data => {
        // Set ImageData.
        const imageData = data.indexImage.childImageSharp.fluid
        return (

          <BackgroundImage
            Tag="section"
            className="hero_img"
            fluid={imageData}
            backgroundColor={`#040e18`}
          >
            <div className="hero-darken">
              <h2 className="hero-text">
                <p>A unique way to explore hidden</p>
                <Typical
                  steps={['beaches.', 1500, 'islands.', 1500, 'caves.', 1500]}
                  loop={Infinity}
                  wrapper="p"
                />
              </h2>
            </div>
          </BackgroundImage>

        )
      }}
    />
  )
}


export default BackgroundSection